<template >
    <div class="tour-info">
        <label  for="date">Select Date</label>
        <br>
        <input type="date" name="" id="date" v-model="date">
        <br>
        <input class="input" style="margin-bottom:1rem ;" type="text" placeholder="Tournament Name" v-model="tourName">
    </div>

    <div class="head" style="margin-top:2rem ; margin-bottom:-2rem;">
        <h1>Enter Details of {{ teamName }}</h1>
    </div>


    <div class="main-wrapper">

        <div class="all">

            <div class="first-match match-wrapper">




                <input type="text" v-model="teamName" placeholder="Enter Team Name">
                <input type="number" placeholder="1st Game Position" v-model.number="m1pp">
                <input type="number" v-model.number="m1kp" placeholder="1st Game Kills">
            </div>

            <div class="second-match match-wrapper">
                <input type="number" v-model.number="m2pp" placeholder="2st Game Position">
                <input type="number" v-model.number="m2kp" placeholder="2st Game Kills">
            </div>

            <div class="third-match match-wrapper">
                <input type="number" v-model.number="m3pp" placeholder="3rd Game Position">
                <input type="number" v-model.number="m3kp" placeholder="3rd Game Kills">
            </div>

            <div class="fourth-match match-wrapper">
                <input type="number" v-model.number="m4pp" placeholder="4rth Game Position">
                <input type="number" v-model.number="m4kp" placeholder="4rth Game Kills">
            </div>

            <div class="fifth-match match-wrapper">
                <input type="number" v-model.number="m5pp" placeholder="5th Game Position">
                <input type="number" v-model.number="m5kp" placeholder="5th Game Kills">
            </div>

            <div class="sixth-match match-wrapper">
                <input type="number" v-model.number="m6pp" placeholder="6th Game Position">
                <input type="number" v-model.number="m6kp" placeholder="6th Game Kills">

            </div>


        </div>
    </div>
    <button class="btn"
        @click="calculate(), team1Total(team1TotalPts, team1PositionPts, team1KillsPts, teamName, tourName, date, booyah)">Team
        2</button>

</template>
<script>
export default {
    name: "team1Vue",
    data() {
        return {
            tourName: "",
            date: "",
            teamName: "Team Name 1",
            booyah: null,
            m1kp: null,
            m1pp: null,
            m2kp: null,
            m2pp: null,
            m3kp: null,
            m3pp: null,
            m4kp: null,
            m4pp: null,
            m5kp: null,
            m5pp: null,
            m6kp: null,
            m6pp: null,
            team1KillsPts: 0,
            team1PositionPts: 0,
            team1TotalPts: 0,
            game: ""
        }
    },
    watch: {

    },
    props: {
        team1Total: Function,
    },
    methods: {
        calculate() {


            let x = Number(this.m1kp) + Number(this.m2kp) + Number(this.m3kp) + Number(this.m4kp) + Number(this.m5kp) + Number(this.m6kp)

            this.team1KillsPts = x


            let pts = [12, 9, 8, 7, 6, 5, 4, 3, 2, 1, 0, 0]

            let m1ppts = Number(this.m1pp)
            let m2ppts = Number(this.m2pp)
            let m3ppts = Number(this.m3pp)
            let m4ppts = Number(this.m4pp)
            let m5ppts = Number(this.m5pp)
            let m6ppts = Number(this.m6pp)

            if (m1ppts === 0 || m1ppts > 12) {
                m1ppts = 12
            }
            if (m2ppts === 0 || m2ppts > 12) {
                m2ppts = 12
            }
            if (m3ppts === 0 || m3ppts > 12) {
                m3ppts = 12
            }
            if (m4ppts === 0 || m4ppts > 12) {
                m4ppts = 12
            }
            if (m5ppts === 0 || m5ppts > 12) {
                m5ppts = 12
            }
            if (m6ppts === 0 || m6ppts > 12) {
                m6ppts = 12
            }
            Number(this.booyah)
            this.m1pp == 1 ? this.booyah += 1 : this.booyah;
            this.m2pp == 1 ? this.booyah += 1 : this.booyah;
            this.m3pp == 1 ? this.booyah += 1 : this.booyah;
            this.m4pp == 1 ? this.booyah += 1 : this.booyah;
            this.m5pp == 1 ? this.booyah += 1 : this.booyah;
            this.m6pp == 1 ? this.booyah += 1 : this.booyah;


            this.team1PositionPts = pts[m1ppts - 1] + pts[m2ppts - 1] + pts[m3ppts - 1] + pts[m4ppts - 1] + pts[m5ppts - 1] + pts[m6ppts - 1]


            this.team1TotalPts = this.team1KillsPts + this.team1PositionPts




        }

    }
}

</script>
