            //     alert("please rotate the screen & Download Again")
            // }
            let table = document.getElementById("table");
            Html2Canvas(table).then((canvas) => {
                const base64imgae = canvas.toDataURL("image/png");
                var anchor = document.createElement("a");
                anchor.setAttribute("href", base64imgae);
                anchor.setAttribute("download", "Result.png");
                anchor.click();
                anchor.remove();






            })
        },
        upload() {
            let results = firebase.database().ref("Results");
            let newResult = this.teams

            results.push(newResult)
        },
        checkStanding() {

            window.scrollTo(0, 0);
            this.teams.forEach((team, i) => {
                team.id = i + 1;

            });

            this.teams.sort((a, b) => {
                if (a.totalpts != b.totalpts) {
                    return b.totalpts - a.totalpts
                } else if (a.totalpts === b.totalpts) {
                    return b.booyah - a.booyah
                } else if (a.booyah === b.booyah) {
                    return b.killpts - a.killpts
                }

            })



        },

