import creatVue from './components/create.vue'
import resultVue from './components/results.vue'
import contactVue from './components/contact.vue'
import pubgVue from './components/createResults/pubg.vue'
import { createRouter, createWebHistory} from 'vue-router';


const routes = [
    {
        name: "Home",
        component: creatVue,
        path: "/"
    },
    {
        name : "Results",
        component: resultVue,
        path : "/results"
    },
    {
        name : "pubg",
        component : pubgVue,
        path : "/pubg"
    },
    {
        name : "contact",
        component : contactVue,
        path : "/contact"
    }

]

const router = createRouter({
    history: createWebHistory(),
    routes
});

export default router;