<template >
    <loaderVue v-if="loading"></loaderVue>
    <section v-if="!loading && !success">
        <div class="container">
            <div class="input-wrapper">
                <h1>Contact Us</h1>
                <input type="text" placeholder="Name" v-model="name">
                <input type="email" placeholder="Email" v-model="email">
                <input type="text" placeholder="Subject" v-model="subject">
                <textarea placeholder="Message" id="" cols="25" rows="10" v-model="message"></textarea>
                <span>{{ error }}</span>
                <button class="btn" @click="submit()">Submit</button>

            </div>
        </div>
    </section>
    <span v-if="success">
        <h3>{{ this.successMsg }}</h3>
        <h3>
            <router-link to="/">Return To Home</router-link>
        </h3>
    </span>


</template>
<script>
import 'firebase/compat/database';
import firebase from 'firebase/compat/app';
// import 'firebase/compat/firestore';
import loaderVue from './loader.vue';
export default {
    name: "contactVue",
    components: {
        loaderVue
    },
    data() {
        return {
            name: "",
            email: "",
            subject: "",
            message: "",
            loading: false,
            error: "",
            successMsg: "",
            success: false

        }
    },
    methods: {
        submit() {
            this.loading = true
            let contact = firebase.database().ref("Contact");
            let newContact = {
                name: this.name,
                email: this.email,
                subject: this.subject,
                message: this.message
            }
            contact.push(newContact)
            this.successMsg = this.name + " " + "Your Responce Successfully recorded. You Will Be Responded Soon"
            this.success = true
            this.loading = false


                



        }
    },
}
</script>
<style scoped>
.input-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

h3 {
    margin-top: 6rem;
    padding: auto 2rem;
}
</style>