<script>
import 'firebase/compat/database';
import firebase from 'firebase/compat/app';
import 'firebase/compat/firestore';
import loaderVue from '../components/loader.vue'
export default {
    name: "resultsVue",
    components: {
        loaderVue
    },
    data() {
        return {
            // results: [],
            arr: [],
            style: "",
            table: "style2",
            tab: "style2",
            loading: false
        }
    },
    methods: {

        getval() {
            this.table = this.style
            this.tab = this.style
        },
    },
    created() {
        this.loading = true
        firebase.database().ref("Results").on('value', (snape) => {
            let x = snape.val()
            // this.results.push(x)
            for (let i in x) {

                this.arr.push(x[i])
            }
            this.loading = false

            // console.log(this.results)
            // console.log(this.arr)
        })
    },
}
